import { TChoosenWord } from '../pages/ChooseWord/types';

export function selectRandomWords(neededElements: number) {
  const selectedWords = [] as TChoosenWord[];

  for (let i = 0; i < neededElements; i += 1) {
    const key = Math.floor(Math.random() * words.length);
    if (key % 2 === 0) {
      selectedWords.push({
        correctWord: words[key],
        word: words[key],
        weight: key,
        key: key.toString(),
      });
    } else {
      const keyWrong = Math.floor(Math.random() * wrongWords.length);
      selectedWords.push({
        correctWord: wrongWords[keyWrong].correct,
        word: wrongWords[keyWrong].misspelled,
        weight: key,
        key: key.toString(),
      });
    }
  }
  return selectedWords;
}

export function addNewWords() {
  let count = 0;
  let idx = 0;
  const newArray: string[] = [];
  for (let i = 0; i < newWords.length; i += 1) {
    if (count < 3) {
      if (!count) {
        newArray[idx] = newWords[i];
      } else {
        newArray[idx] += `,${newWords[i]}`;
      }
      count += 1;
    } else {
      count = 1;
      idx += 1;
      newArray[idx] = newWords[i];
    }
  }
  return newArray;
}

